function timer() {
  let deadline; //   

  promotionTime(); //    
  setClock('.timer', deadline); //  

  function promotionTime() { //    
    if (localStorage.getItem('deadline') && Date.parse(localStorage.getItem('deadline')) > Date.parse(new Date())) {
      //        ,    
      deadline = localStorage.getItem('deadline'); //   
    } else { //           
      const deadlineCalc = new Date(Date.now() + (1 * 24 * 60 * 60 * 1000)); //       + 1
      deadline = new Date(deadlineCalc.getFullYear(), deadlineCalc.getMonth(), deadlineCalc.getDate(), 0, 0, 0); //       deadlineCalc
      localStorage.setItem('deadline', deadline); //      
    }

    const promotionEndText = new Date(deadline).toLocaleString('ru', {
      day: '2-digit',
      month: 'long',
    }); //     2      ,    

    document.querySelector('#promotion-end').textContent = `  ${promotionEndText}  00:00`; //    
  }

  function getTimeRemaining(endtime) { //        endtime
    const t = Date.parse(endtime) - Date.parse(new Date()); //       
    const days = Math.floor(t / (1000 * 60 * 60 * 24)); //   
    const hours = Math.floor((t / (1000 * 60 * 60)) % 24); //         24
    const minutes = Math.floor((t / (1000 * 60)) % 60); //         60
    const seconds = Math.floor((t / 1000) % 60); //         60

    return { //     
      total: t,
      days: days,
      hours: hours,
      minutes: minutes,
      seconds: seconds,
    };
  }

  function getZero(num) { //     ,    0
    if (num >= 0 && num < 10) {
      return `0${num}`;
    }
    return num;
  }

  function setClock(timerBlock, endtime) { //  
    const timer = document.querySelector(timerBlock); //    
    const days = timer.querySelector('#days'); //   
    const hours = timer.querySelector('#hours'); //   
    const minutes = timer.querySelector('#minutes'); //   
    const seconds = timer.querySelector('#seconds'); //   

    const timeInterval = setInterval(updateClock, 1000); //      

    updateClock(); //   ,    1   

    function updateClock() { //  
      const t = getTimeRemaining(endtime); //     

      days.textContent = getZero(t.days);
      hours.textContent = getZero(t.hours);
      minutes.textContent = getZero(t.minutes);
      seconds.textContent = getZero(t.seconds);

      if (t.total <= 0) {
        clearInterval(timeInterval); //   ,    0
      }
    }
  }
}

export default timer;