import IMask from 'imask';
import {modalShow, modalHide} from './modal';
import {postData} from '../services/services';

function forms() {
  const formTags = document.querySelectorAll('form'); //   
  const modalBlock = document.querySelector('.modal'); //   html   .hide
  const formInputs = document.querySelectorAll('form input');

  const phoneInputs = document.querySelectorAll('[name=phone]');

  phoneInputs.forEach((item) => { //    
    IMask(item, {
      mask: '+{7}(000)000-00-00',
    });
  });

  formInputs.forEach((inputElem) => { //     input
    const errorElem = inputElem.nextElementSibling; //    input   span  
    inputElem.addEventListener('focus', () => {
      errorElem.textContent = ''; //   
      errorElem.className = 'error'; //    
      inputElem.style.borderColor = 'unset'; //   
    });
    inputElem.addEventListener('blur', () => {
      validCheck(inputElem, errorElem);
    });
  });

  formTags.forEach((item) => {
    bindPostData(item); //      
  });

  function validCheck(inputElem, errorElem) {

    errorElem.textContent = ''; //   
    if (!inputElem.validity.valid) {
      showError(inputElem, errorElem); //    
      inputElem.style.borderColor = 'red';
      return false;
    }
    return true;
  }

  function showError(inputElem, errorElem) { //    
    if (inputElem.validity.valueMissing) { //      
      errorElem.textContent = ' ';
    } else if (inputElem.validity.tooShort) {
      errorElem.textContent = `     ${inputElem.minLength} `;
    } else if (inputElem.validity.patternMismatch && inputElem.matches('[type="phone"]')) {
      //  email       type="email"   
      errorElem.textContent = '  ';
    }
    errorElem.className = 'error_active';
  }

  function bindPostData(form) {
    form.addEventListener('submit', (event) => { //        enter
      event.preventDefault();

      const eventInputs = event.target.querySelectorAll('input'); //  input  ,   
      eventInputs.forEach((input) => validCheck(input, input.nextElementSibling)); //   input        
      for (const input of eventInputs) {
        if (!validCheck(input, input.nextElementSibling)) { //    ,       
          return;
        }
      }

      const spinner = document.createElement('img'); //     
      spinner.setAttribute('src', 'img/form/spinner.svg');
      spinner.style.cssText = `
        display: block;
        margin: 0 auto;
      `;
      form.insertAdjacentElement('afterend', spinner); //      

      const formData = new FormData(form); //   ,   input     name=""

      const json = JSON.stringify(Object.fromEntries(formData.entries()));
      //       ,    ,    JSON

      postData('http://localhost:3000/requests', json) //      
        .then((data) => { //   promise
          console.log(data); //   
          showThanksModal('!     ');
          spinner.remove(); //   
        })
        .catch(() => { //  reject ()
          showThanksModal('-   ...');
        })
        .finally(() => { //    
          form.reset(); //  
        });
    });
  }

  function showThanksModal(text) { //       
    const formModalDialog = document.querySelector('.modal__dialog');

    formModalDialog.classList.remove('show');
    formModalDialog.classList.add('hide'); //     
    modalShow('.modal'); //       

    const thanksModalDialog = document.createElement('div'); //      text
    thanksModalDialog.classList.add('modal__dialog');
    thanksModalDialog.innerHTML = `
    <div class="modal__content">
      <div data-close class="modal__close">&times;</div>
      <div class="modal__title">${text}</div>
    </div>
    `;
    modalBlock.append(thanksModalDialog); //       
    setTimeout(() => { //  4        
      thanksModalDialog.remove();
      formModalDialog.classList.remove('hide');
      formModalDialog.classList.add('show');
      modalHide('.modal');
    }, 4000);
  }
}

export default forms;