function tabs(tabContentBlockSelector, tabHeaderContainerSelector, tabHeaderItemSelector) {
  const tabContentBlocks = document.querySelectorAll(tabContentBlockSelector); //  
  const tabHeaderContainer = document.querySelector(tabHeaderContainerSelector); //    
  const tabHeaderItems = document.querySelectorAll(tabHeaderItemSelector); //   

  hideTabContent(); //   
  showTabContent(); //      (i = 0   showTabContent)

  tabHeaderContainer.addEventListener('click', (event) => { //      
    if (event.target && event.target.matches(tabHeaderItemSelector)) { //     
      tabHeaderItems.forEach((item, i) => { //  
        if (item === event.target) {
          hideTabContent();
          showTabContent(i); //     
        }
      });
    }
  });

  function hideTabContent() { //   
    tabContentBlocks.forEach((item) => {
      item.classList.add('hide'); // display: none;
      item.classList.remove('show', 'fade');
    });
    tabHeaderItems.forEach((item) => {
      item.classList.remove(`${tabHeaderItemSelector.slice(1)}_active`);
      //      . slice      
    });
  }

  function showTabContent(i = 0) { //   
    tabContentBlocks[i].classList.add('show', 'fade'); // display: block;
    tabContentBlocks[i].classList.remove('hide');
    tabHeaderItems[i].classList.add(`${tabHeaderItemSelector.slice(1)}_active`);
    //      . slice      
  }
}

export default tabs;