function slider({
  sliderBlockSelector,
  currentSelector,
  totalSelector,
  innerSelector,
  wrapperSelector,
  slideSelector,
  navigationSelector,
  dotSelector,
  nextArrowSelector,
  prevArrowSelector,
}) {
  const sliderBlock = document.querySelector(sliderBlockSelector); // '.offer__slider'
  const current = document.querySelector(currentSelector); // '#current'
  const total = document.querySelector(totalSelector); // '#total'
  const inner = document.querySelector(innerSelector); // '.offer__slider-inner'
  const wrapper = document.querySelector(wrapperSelector); // '.offer__slider-wrapper'
  const slides = document.querySelectorAll(slideSelector); //    ,   nodelist // '.offer__slide'
  const nav = document.querySelector(navigationSelector); //    // '.offer__slider-navigation'

  const transition = 0.75; //       
  const wrapperWidth = parseInt(window.getComputedStyle(wrapper).width, 10); // parseInt  'px'
  const threshold = wrapperWidth * 0.33; // ,     =     
  let mouseStart = 0; //   
  let mouseMove = 0; //  
  let initialPos; //     
  let dots; // 
  let slideIndex = 1; //  
  let canSlide = true; //  

  sliderInitialization(); //       
  sliderBtnsClicks(); //     
  sliderDragActions(); //   

  function getZero(num) { //     ,    0
    if (num >= 0 && num < 10) {
      return `0${num}`;
    }
    return num;
  }

  function sliderInitialization() { //       
    const firstSlideClone = document.createElement('div'); //         
    firstSlideClone.classList.add(`${slideSelector.slice(1)}`); // slice    
    firstSlideClone.innerHTML = slides[0].innerHTML;
    inner.append(firstSlideClone);

    const lastSlideClone = document.createElement('div'); //         
    lastSlideClone.classList.add(`${slideSelector.slice(1)}`); // slice    
    lastSlideClone.innerHTML = slides[slides.length - 1].innerHTML;
    inner.prepend(lastSlideClone);

    for (let i = 0; i < slides.length; i += 1) { //     
      const dot = document.createElement('div');
      dot.classList.add(`${dotSelector.slice(1)}`); // slice    
      nav.append(dot);
      dots = document.querySelectorAll(dotSelector); // '.offer__slider-dot'
    }

    inner.style.width = `${wrapperWidth * (slides.length + 2)}px`; //     , +2   ,    slides
    total.textContent = getZero(slides.length); //     (   )
    currentAndDots(); //       
    showSlide(); //    
  }

  function clickDelay() { //        
    canSlide = false;
    setTimeout(() => {
      canSlide = true;
    }, transition * 1000); //    
  }

  function currentAndDots() { //       
    current.textContent = getZero(slideIndex); // getZero  ,    1 
    dots.forEach((dot) => dot.classList.remove(`${dotSelector.slice(1)}_active`)); //      
    dots[slideIndex - 1].classList.add(`${dotSelector.slice(1)}_active`); //    
  }

  function showSlide() { //    
    inner.style.transform = `translateX(${-wrapperWidth * slideIndex}px)`; //  
  }

  function addAnimation() {
    inner.style.transition = `all ${transition}s`; //    
  }

  function removeAnimation() {
    inner.addEventListener('transitionend', () => inner.style.transition = 'none'); //      ;
  }

  function nextSlide() {
    addAnimation(); //    
    slideIndex += 1;
    if (slideIndex > slides.length) { //    
      slideIndex = 1; //    
      inner.style.transform = `translateX(${-wrapperWidth * (slides.length + 1)}px)`; //   1
      inner.addEventListener('transitionend', () => { //   
        inner.style.transition = 'none'; //  ,      
        inner.style.transform = `translateX(${-wrapperWidth * slideIndex}px)`; //    
      });
    } else {
      showSlide(); //    
      removeAnimation(); //      ;
    }
    currentAndDots(); //       
    clickDelay(); //   1
  }

  function prevSlide() {
    addAnimation(); //    
    slideIndex -= 1;
    if (slideIndex < 1) { //   1 
      slideIndex = slides.length; //    
      inner.style.transform = `translateX(${0}px)`; //    
      inner.addEventListener('transitionend', () => { //   
        inner.style.transition = 'none'; //  ,      
        inner.style.transform = `translateX(${-wrapperWidth * slideIndex}px)`; //    
      });
    } else {
      showSlide(); //    
      removeAnimation(); //      ;
    }
    currentAndDots(); //       
    clickDelay(); //   1
  }

  function sliderBtnsClicks() { //     
    sliderBlock.addEventListener('click', (event) => {
      event.preventDefault();
      if (canSlide === true) { //   ,    
        if (event.target && event.target.closest(nextArrowSelector) && sliderBlock.contains(event.target.closest(nextArrowSelector))) {
          // closest     (  ),  . contains ,  .    
          nextSlide();
        } else if (event.target && event.target.closest(prevArrowSelector) && sliderBlock.contains(event.target.closest(prevArrowSelector))) {
          prevSlide();
        } else if (event.target && event.target.closest(dotSelector) && sliderBlock.contains(event.target.closest(dotSelector))) { //   
          addAnimation(); //    
          dots.forEach((item, i) => {
            if (item === event.target) { //  ,   
              slideIndex = i + 1;
              currentAndDots(); //       
              showSlide(); //    
              removeAnimation(); //      ;
            }
          });
        }
      }
    });
  }

  function sliderDragActions() { //   
    inner.addEventListener('mousedown', (event) => { //  
      // ,     css  transition, c   .          
      event.preventDefault(); //      
      if (canSlide === true) { //   ,    
        mouseStart = event.clientX; //    
        initialPos = +inner.style.transform.match(/[-0-9.]+/)[0]; // translateX   

        inner.addEventListener('mousemove', dragAction); //  
        inner.addEventListener('mouseup', dragEnd); //  
        inner.addEventListener('mouseout', dragEnd); //  ,      
      }
    });
  }

  function dragAction(event) {
    mouseMove = mouseStart - event.clientX; //   onmousemove       1px   
    mouseStart = event.clientX; //      ,   

    const currentTransform = +inner.style.transform.match(/[-0-9.]+/)[0];
    //   translateX, .   "translateX:"    ,   ,
    // +        1   .    ,  0 

    inner.style.transform = `translateX(${currentTransform - mouseMove}px)`;
  }

  function dragEnd() {
    const finalPos = +inner.style.transform.match(/[-0-9.]+/)[0]; // translateX   

    if (-finalPos - -initialPos === 0) { //    ,  
      inner.style.transition = 'none';
    } else if (-finalPos - -initialPos > threshold) { //      ,    
      nextSlide();
    } else if (-finalPos - -initialPos < -threshold) { //      ,    
      prevSlide();
    } else { //    ,    ,    
      inner.style.transition = `all ${transition}s`; //    
      inner.style.transform = `translateX(${initialPos}px)`;
      inner.addEventListener('transitionend', () => inner.style.transition = 'none'); //      ;
      clickDelay(); //  
    }

    inner.removeEventListener('mousemove', dragAction); //    
    inner.removeEventListener('mouseup', dragEnd);
    inner.removeEventListener('mouseout', dragEnd);
  }
}

export default slider;

