function calc() {
  const result = document.querySelector('.calculating__result span'); //    :

  let sex;
  let height;
  let weight;
  let age;
  let ratio;

  initLocalSettings('#gender div', 'calculating__choose-item_active'); //         localStorage
  initLocalSettings('.calculating__choose_big div', 'calculating__choose-item_active'); //         localStorage
  calcTotal(); //     ,  ,      
  getStaticInformation('#gender div', 'calculating__choose-item_active'); //   
  getStaticInformation('.calculating__choose_big div', 'calculating__choose-item_active'); //    
  getDynamicInformation('#height'); //    
  getDynamicInformation('#weight'); //    
  getDynamicInformation('#age'); //    

  function initLocalSettings(selector, activeClass) { //         localStorage
    const elements = document.querySelectorAll(selector);

    if (localStorage.getItem('sex')) { //   localStorage  ,  
      sex = localStorage.getItem('sex');
    } else {
      sex = 'female';
      localStorage.setItem('sex', sex); //   localStorage  ,    
    }

    if (localStorage.getItem('ratio')) { //   localStorage  ,  
      ratio = localStorage.getItem('ratio');
    } else {
      ratio = 1.375;
      localStorage.setItem('ratio', ratio); //   localStorage  ,    
    }

    document.querySelectorAll('.calculating__choose-item').forEach((item) => { //    
      item.value = '';
    });

    elements.forEach((elem) => {
      elem.classList.remove(activeClass); //      
      if (elem.getAttribute('id') === localStorage.getItem('sex')) { //          localStorage
        elem.classList.add(activeClass);
      }
      if (elem.getAttribute('data-ratio') === localStorage.getItem('ratio')) { //          localStorage
        elem.classList.add(activeClass);
      }
    });
  }

  function calcTotal() { //     
    if (!sex || !height || !weight || !age || !ratio) {
      result.parentElement.style.visibility = 'hidden'; //     ,   c 
      return; //   
    }

    result.parentElement.style.visibility = 'visible'; //   c 

    if (sex === 'female') { // https://fitseven.ru/zdorovie/metabolism/sutochnaya-norma-kaloriy
      result.textContent = Math.round((447.6 + (9.2 * weight) + (3.1 * height) - (4.3 * age)) * ratio);
    } else {
      result.textContent = Math.round((88.36 + (13.4 * weight) + (4.8 * height) - (5.7 * age)) * ratio);
    }
  }

  function getStaticInformation(selector, activeClass) { //      
    const elements = document.querySelectorAll(selector); //     parentSelector

    elements.forEach((item) => {
      item.addEventListener('click', (event) => {
        if (event.target.getAttribute('data-ratio')) { //       data-ratio="..."
          ratio = +event.target.getAttribute('data-ratio'); //   
          localStorage.setItem('ratio', ratio);
        } else { //   
          sex = event.target.getAttribute('id'); //    id
          localStorage.setItem('sex', sex);
        }

        elements.forEach((elem) => { //      
          elem.classList.remove(activeClass);
        });

        event.target.classList.add(activeClass); //     

        calcTotal();
      });
    });
  }

  function getDynamicInformation(selector) { //    
    const input = document.querySelector(selector);

    input.addEventListener('input', () => {

      if (input.value.match(/\D/)) { //     
        input.style.border = '1px solid red'; //  
      } else {
        input.style.border = 'none';
      }

      switch (input.getAttribute('id')) {
        case 'height':
          height = +input.value;
          break;
        case 'weight':
          weight = +input.value;
          break;
        case 'age':
          age = +input.value;
          break;
      }

      calcTotal();
    });
  }
}

export default calc;